.contact-container {
  background: url(../../assets/contactSmall.jpg) center center/cover no-repeat;
  background-attachment: fixed;
  height: 100vh;
  align-items: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (min-width: 2560px) {
  .contact-container {
    background: url(../../assets/contact.jpg) center center/cover no-repeat;
  }
}

.fa-github {
  padding: 1rem;
  font-size: 5rem;
}

.fa-github:hover {
  color: blue;
  height: 5.5rem;
  transition: 0.2s ease-out;
}

